.container{
    display: flex;
    
    background-color: black;
    
    width: 83%;
    float: right;    
    color: white;    
    font-family: 'Share Tech Mono', monospace;
    justify-content: space-around;
    height: 110%;
    min-height: 1050px;
    
}

#textHAB{  
    display: flex;
    
    align-items: flex-start;
    text-align: start; 
    position: relative;
    top: 30px;

    
    
    
}

.hab2{
    display: flex;
    flex-direction: column;
    font-size: 20px;
    max-width: 800px;
    margin-left: 120px; 
}




.hab{
    flex:1;
    display: flex;
    flex-direction: column;
    font-size: 20px;
    max-width: 350px;
    width: 100%;
    
    
    
   
}



@media only screen and (max-width: 800px) {

    .container{
        justify-content: center;
        align-items: center;
        
        margin: auto;
        font-size: 10px;
        width: 100%;
        display: flex;
        flex-direction: column;
        
    }

    .hab{
        font-size: 14px;
        display: flex;
        justify-content: center;
        width: 340px;
        align-items: center;
        padding: 10px;
        text-align: center;
        
    }

    .hab2{
        display: flex;
        margin-left: 0px;
        font-size: 14px;
        display: flex;
        justify-content: center;
        width: 340px;
        align-items: center;
        padding: 10px;
        text-align: center;
       
    }

    .hab p{
        margin-right:30px;
    }

}


@media only screen and (min-width: 800px) and (max-width:1300px) {


    .container{
        width: 100%;
    }

}