@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
.MenuOpen{
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 20%;
    background-color: rgb(141, 25, 25);
    align-items: center;
    padding: 10px;
    justify-content: space-around;
    transition: 0.5s;
    z-index: 1;
    position: fixed;
    
   
    

}

.copyright{
    color: white;
}

.texxt{
    color: white;
    font-size: 23px;
    font-weight: bold;
    font-family: 'Roboto', sans-serif;;
}

.MenuClosed{
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 250px;
    background-color: rgb(149, 170, 240);
    align-items: center;
    padding: 10px;
    justify-content: space-around;
    transform:translate(-300px);
    transition: 0.5s;
    position: absolute;
    

}


.photo{
    background-color: black;
    width: 200px;
    height: 200px;
    border-radius: 150px;
    background-image: url('./../../images/bb.jpg');
    background-position:center;
    background-size: 130%;
    background-repeat: no-repeat;
    border: 6px solid black;
}

.MenuOp{
    font-size: 20px;
    display: flex;
    height: 60%;
    font-family: 'Roboto', sans-serif;
    margin-bottom: 20px;
    font-weight: 800;
    margin-right: 20%;
    
}

.MenuOp ul{
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    list-style: none;
    font-weight: bold;
    
    
}

.MenuOp ul li{
    cursor: pointer;
    width: 200px;
    height: 40px;
    font-weight: bold;
    text-align: center;
    padding: 10px;
    font-weight: bold;
    color: rgb(255, 255, 255);
    align-items: center;
    
    
}

.MenuOp ul li:hover{
   transform: scale(1.2);
   transition: 0.5s;
}

.MenuOp .icons{
    display: flex;
    justify-content: space-around;
    width: 100%;
    height: 34px;
    padding: 0px;
    margin-top: 30px;
    cursor: pointer;
    transition: 0.5s;
    text-align: center;
    align-items: center;
}

.icons .icon1, .icon2, .icon3{
    width: 30px;
    height: 30px;
    text-align: center;
    align-items: center;
}

.icons .icon1:hover, .icon2:hover, .icon3:hover{
    transition: 0.5s;
    transform: scale(1.4);
 }

 .iconss{
    margin: 0px 5px;
    height: 23px;
    position: relative;
    right: 8px;
}



.displayMenu{
    display: none;
    
}

.btn{
    height: 30px;
    width: 30px;
    margin: 10px;
    border-radius: 10px;
    padding: 4px;
}

.btn:hover{
    transform:scale(1.2);
    transition: 0.5s;
}


@media only screen and (max-width: 1200px) {
    .displayMenu {
      display: block;
      position: fixed;
      z-index: 2;
    }

    .MenuOpen{
        display: flex;
        flex-direction: column;
        height: 100vh;
        width: 250px;
        background-color: rgb(141, 25, 25);
        align-items: center;
        padding: 10px;
        justify-content: space-around;
        transition: 0.5s;
        
       
        
    
    }
    
    
  }
