.container{
    float: right;
    display: flex;
    justify-content: center;
    color: rgb(255, 255, 255);
    min-height:800px;
    height: 100%;
    background-color: rgb(0, 0, 0);
    width: 83%;
    flex-direction: column;
    padding: 20px;

    
}

.form{
    justify-content: center;
    align-items: center;
    background-color: rgb(31, 28, 28);
    width: 60%;
    margin: auto;
    border-radius: 15px;
    min-height: 600px;
    height: 100%;
    display: flex;
    flex-direction: column;
    
}

.inputContainer{
    margin: 20px;
}

.input{
    
    color: white;
    background-color: rgb(48, 45, 45);
    height: 60px;
    padding: 20px;
    
    width: 70%;
    text-decoration: none;
    outline: none;
    transition: 0.5s;
    border-radius: 5px;
    font-size: 15px;
    margin: 20px;
}

.btn{
    background-color: rgb(46, 39, 39);
    width: 150px;
    height: 35px;
    text-align: center;
    align-items: center;
    display: flex;
    justify-content: center;
    transition: 0.5s;
    font-size: 20px;
}

.btn:hover{
    
    background-color:rgb(24, 21, 21);
    transform: scale(1.1);
}

.inputMsg{
    min-height: 90px;
    height: 20vh;
    width: 70%;
    text-decoration: none;
    outline: none;
    border-radius: 5px;
    background-color: rgb(48, 45, 45); ;
    margin: 20px;
    font-size: 15px;
    padding: 5px;
    color: white;
    padding: 10px;
    
    text-align:start;
    

}


.myButton {
    margin-top: 30px;
    padding: 7px;
    text-align: center;
    align-items: center;
	box-shadow:inset 0px 1px 0px 0px #cf866c;
	background:linear-gradient(to bottom, #d0451b 5%, #bc3315 100%);
	background-color:#d0451b;
	border-radius:3px;
	border:1px solid #942911;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:13px;
	width: 300px;
    height: 50px;
    font-size: 30px;
	text-decoration:none;
	text-shadow:0px 1px 0px #854629;
}
.myButton:hover {
	background:linear-gradient(to bottom, #bc3315 5%, #d0451b 100%);
	background-color:#bc3315;
}
.myButton:active {
	position:relative;
	top:1px;
}


@media only screen and (max-width: 800px) {
    .container{
        width: 100%;
    }

    .form{
        margin: auto;
        width: 95%;
        height: 90%;
    }
}

        



@media only screen and (min-width: 800px) and (max-width:1300px) {

    .container{
        width: 100%;
    }
}

