.container{
    width: 100%;
    background-color: rgb(247, 247, 239);
    text-align: center;
    align-items: center;
    display: flex;
    justify-content: flex-end;
    min-height: 800px;
    height: 100%;
    position: relative;
    
}

.card{
    background-color: #fff;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    width: 75%;
    margin-top: 10px;
    height: 500px;
    margin-right: 30px;
    display: flex;
    justify-content: space-around;
    
    transition: 0.3s;
}



.photo{
    background-color: black;
    width: 30%;
    height: 500px;
    background-size: 100%;
    background-repeat: no-repeat;
    background-image: url('./../../images/xavatar.png');
    background-position: 50%;

}

.descricao{
    width: 70%;
    text-align: center;
    padding: 20px;
    font-size: 18px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.sobre{
    display: flex;
    flex-direction: column;
    align-items: center;
}


.text{
    font-size: 20px;
}


  
.curriculo{
  font-size: 25px;
  border: 1px solid black;
  padding: 10px;
  cursor: pointer;
  margin-top: 20px;
  font-weight: bold;
  transition: 1s;
}

.curriculo:hover{
  background-color: red;
  color: white;
}


  @media only screen and (max-width: 800px) {

    .descricao{
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin-left: 10%;
        font-size: 12px;
        width: 100%;
        text-align: center;
        margin: auto;
    }

    .container{
        flex-direction: column;
        margin-left: 20px;
        


    }

    .card{
        flex-direction: column; 
        height: 115vh;
        position:absolute;
        top: 0;
        width: 85%;
    
       
    }

    .sobre{
        margin: auto;
        flex-direction: column;
        text-align: center;
        align-items: center;
        
        
    }

    .text{
        font-size: 20px;
        width: 100%;
    }

    .photo{
        flex-direction: column;
        width: 100%;
        background-size: 55%;
        
    }

    .curriculo{
        height: 40px;
        width: 210px;
        align-items: center;
        display: flex;
        justify-content: center;
        margin-left: 15px;
    }
  }


  @media only screen and (min-width: 400px) and (max-width:1300px) {

    

    .card{
        
        flex-direction: column; 
        height: 95%;
        position:absolute;
        top: 0;
        width: 90%;
    }

    .photo{
        width: 100%;
        background-size: 55%;
    }

    .descricao{
        margin: auto;
        
    }

    .text{
        font-size: 20px;
        width: 100%;
    }

  }