.container{
    height: 100%;
    padding: 30px;
    width: 80%;
    background-color: rgb(243, 242, 242);
    font-family: 'Share Tech Mono', monospace;
    text-align: center;
    float: right;
    position: relative;
    align-items: center;
    text-align: center;
    justify-content: center;
    
    
}

.projetos{
    display: flex;
    margin: 20px;
    align-items: center;
        text-align: center;
        justify-content: center;
    
    
    
    
}

.imgContainer{
    height: 100%;
    width: 100%;
}

.box{
   
    display: flex;
    width: 50%;
    background-size: 100%;
    height: 250px;
    background-repeat: no-repeat;
    position: relative;
    transition: 0.5s;
    cursor: pointer;
    border-radius: 10px;
    margin: 20px;
}

.box:hover{
    transform: scale(1.1);
    transition: 0.5s;
}

.box p{
    font-size: 18px;
    text-align: center;
    align-items: center;
    display: flex;
    justify-content: center;
    margin-left: 15px;
    height:50px;
}


.containerText{

    height: 30%;
    border-top-right-radius: 20px;
    color: black;
    background-color: WHITE;
    -webkit-box-shadow:0px 1px 1px #3a303a;
 -moz-box-shadow:0px 1px 1px #3a303a;
 box-shadow:0px 1px 7px #3a303a;
    width: 50%;
    position: absolute;
    bottom: 0;
    

    
}

.img{
    position: absolute;
    left: 0;
    top: 25px;
    height: 40px;
    
}



@media only screen and (min-width: 400px) and (max-width:1100px) {

    .projetos{
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        justify-content: center;
    }

    .container{
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        justify-content: center;
    }

    .box{
        flex-direction: column;
        width: 100%;
        
    }

    .box p{
        font-size: 13px;
    }

}







