@import url('https://fonts.googleapis.com/css2?family=Fugaz+One&family=Share+Tech+Mono&display=swap');



.text {
    
    width: 100%;
    height: 80vh;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    
    text-transform: uppercase;
    font-family: verdana;
    font-size: 1.9em;
    font-weight: 700;
    color: #f5f5f5;
    text-shadow: 1px 1px 1px #701d1d,
        1px 2px 1px #701d1d,
        1px 3px 1px #701d1d,
        1px 4px 1px #701d1d,
        1px 5px 1px #701d1d,
        1px 6px 1px #701d1d,
        1px 7px 1px #701d1d,
        1px 8px 1px #701d1d,
        1px 9px 1px #701d1d,
        1px 10px 1px #701d1d,
    1px 18px 6px rgba(16,16,16,0.4),
    1px 22px 10px rgba(16,16,16,0.2),
    1px 25px 35px rgba(16,16,16,0.2),
    1px 30px 60px rgba(16,16,16,0.4);
}
	
@media only screen and (min-width: 300px) and (max-width:1000px) {
    .text{
        position: relative;
        margin: auto;
        font-size: 100%;
        width: 80%;
        
    }
    

}

